import { getAuth } from "@clerk/remix/ssr.server";
import { json, LoaderArgs, redirect } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import DashHeader from "~/components/dash-header";
import { ApiKey, getApiKeyByUser } from "~/db/apikey.server";

export async function loader({ request }: LoaderArgs) {
    const { userId } = await getAuth(request);
    
    if(!userId) {
        return redirect("/sign-up");
    }
    const apiKey = await getApiKeyByUser(userId) as ApiKey;
    return json({ apiKey });
}

export default function DashboardDeveloperRoute() {

    const { apiKey } = useLoaderData();

    return (
        <div>
            <DashHeader title="Developer" />
            <p className="mt-4 font-light">In order to use the Tip de el dia service, send the value of your Api Key as a query parameter in the request</p>
        </div>
    );
}